.rm-menu{width:190px;font-size:13px;margin:0;padding:5px 0;z-index:9999999;position:fixed;display:block;border:.3px solid #aaa;border-radius:3px;box-sizing:border-box;background-color:#fff;box-shadow:0 2px 12px 0 rgba(0,0,0,.1);-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.rm-menu li{list-style:none}.rm-menu .rm-menu-hr{border-bottom:1px solid rgba(0,0,0,.1);padding:0;margin:5px 0}.rm-menu .rm-menu-li{padding:3px 10px;cursor:default}.rm-menu .rm-menu-li:before{width:20px;display:inline-block}.rm-menu .rm-menu-li:not(.fa):before{content:""}.rm-menu .rm-menu-li.fa{display:block}.rm-menu .rm-menu-li.fa:before{color:#4dbbff}.rm-menu .rm-menu-li:hover{background-color:#91c9f7}.rm-menu .rm-menu-disabled{color:rgba(0,0,0,.6);cursor:not-allowed}.rm-menu .rm-menu-disabled:hover{background-color:#fff}.rm-menu .rm-menu-a{cursor:pointer}.rm-menu .rm-menu-a a{display:block;width:100%;height:100%;color:#222;text-decoration:none}.rm-menu .rm-menu-list::after{content:">";font-family:宋体;font-weight:600;float:right;margin-top:2px;margin-right:-5px;width:15px;height:15px}.rm-menu.theme-dark,.rm-menu.theme-dark .rm-menu{width:270px;font-size:13px;color:rgba(255,255,255,.95);background-color:#2b2b2b}.rm-menu.theme-dark .rm-menu-li,.rm-menu.theme-dark .rm-menu .rm-menu-li{padding:8px 10px}.rm-menu.theme-dark .rm-menu-li.fa:before,.rm-menu.theme-dark .rm-menu .rm-menu-li.fa:before{color:rgba(255,255,255,.95)}.rm-menu.theme-dark .rm-menu-li:hover,.rm-menu.theme-dark .rm-menu .rm-menu-li:hover{background-color:rgba(255,255,255,.2)}.rm-menu.theme-dark .rm-menu-disabled,.rm-menu.theme-dark .rm-menu .rm-menu-disabled{color:rgba(255,255,255,.7)}.rm-menu.theme-dark .rm-menu-disabled:hover,.rm-menu.theme-dark .rm-menu .rm-menu-disabled:hover{background-color:inherit}.rm-menu.theme-dark .rm-menu-hr,.rm-menu.theme-dark .rm-menu .rm-menu-hr{border-bottom:1px solid rgba(255,255,255,.3)}